:root {
  --content-width: 1240px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + var(--container-offset) * 2);
}

/* stylelint-disable */ /* stylelint-disable */
html {
  overflow-x: hidden;
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
}

*,
*::before,
*::after {
  outline-color: rgba(167, 197, 223, 0.2);
  box-sizing: inherit;
}

body {
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.4;
  color: #41404e;
  background-color: #fff;
}

img,
video {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

input,
button,
select,
textarea {
  font: inherit;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  height: 100%;
  max-width: var(--container-width);
}
.container--lg {
  max-width: calc(var(--container-width) + 100px);
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.disable-scroll {
  overflow: hidden;
}
.disable-scroll body {
  overflow: hidden;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background: #fff;
}
.input-reset[type=number] {
  -moz-appearance: textfield;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-outer-spin-button, .input-reset::-webkit-inner-spin-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.text-center {
  text-align: center;
}

.section {
  counter-increment: section;
  overflow: hidden;
}

.section-counter {
  position: relative;
  display: flex;
  align-items: center;
  font-family: "Prosto One", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  color: #222;
}
.section-counter::before {
  content: "(0" counter(section) ")";
  margin-right: 10px;
}
.section-counter::after {
  content: "";
  display: block;
  width: 27px;
  height: 12px;
  background: url("../img/counter-icon.svg") center no-repeat;
}

.title {
  font-family: "Prosto One", sans-serif;
  font-weight: 400;
  font-size: 52px;
  line-height: 1.24;
  text-transform: uppercase;
  color: #08080d;
}

.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 8;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(37, 30, 30, 0.5);
  opacity: 0;
  transition: 0.3s ease-in-out;
}
.overlay.is-active {
  opacity: 1;
}

.burger {
  position: relative;
  display: none;
  border: none;
  padding: 0;
  width: 18px;
  height: 14px;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  border-radius: 2px;
  width: 100%;
  height: 2px;
  background-color: currentcolor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - 2px);
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  border-radius: 2px;
  width: 100%;
  height: 2px;
  background-color: currentcolor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger.is-active {
  z-index: 9;
}
.burger.is-active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger.is-active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger.is-active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

@media screen and (max-width: 767px) {
  .burger {
    display: block;
  }
}
