.hero {
  padding-bottom: 70px;
  background: #d9e8f5;

  &__inner {
    display: flex;
  }

  &__title {
    margin-bottom: 8px;
    width: 820px;
    font-family: "Prosto One", sans-serif;
    font-weight: 400;
    font-size: 57px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    text-transform: uppercase;
    color: #08080d;
  }

  &__left {
    position: relative;
    z-index: 2;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 20px;
    padding-top: 54px;
    width: 50%;
  }

  &__text {
    margin-bottom: 65px;
  }

  &__btn {
    margin-bottom: 30px;
  }

  &__benefits {
    margin-top: auto;
  }

  &__right {
    flex-shrink: 0;
    width: 50%;
  }

  &__images {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;

    picture {
      display: flex;
    }
  }

  &__img {
    picture {
      display: flex;
    }

    img {
      border-radius: 16px;
    }
  }

  &__img--main {
    position: relative;
    margin-bottom: 20px;

    &::before {
      content: "";
      position: absolute;
      left: 50px;
      bottom: 70px;
      width: 89px;
      height: 89px;
      background: url("../img/hero-decor-1.png") center / cover no-repeat;
    }

    &::after {
      content: "";
      position: absolute;
      right: 38px;
      top: 54px;
      width: 100px;
      height: 100px;
      background: url("../img/hero-decor-2.png") center / cover no-repeat;
    }
  }

  &__img--decor {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      right: 40px;
      top: -18px;
      width: 94px;
      height: 94px;
      background: url("../img/hero-decor-3.png") center / cover no-repeat;
    }
  }
}

.hero-benefits {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border: 1px solid #fff;
  border-radius: 16px;
  padding: 28px;
  background: rgba(255, 255, 255, 70%);
  gap: 14px 28px;

  &__item {
    display: flex;
    align-items: center;

    &:nth-child(1),
    &:nth-child(2) {
      align-items: flex-start;
    }
  }

  &__icon {
    @include icon;

    margin-right: 14px;

    picture {
      display: flex;
    }
  }

  &__text {
    font-size: 15px;
    line-height: 1.34;

    b,
    strong {
      font-weight: 700;
    }
  }
}
