.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  height: 100%;
  max-width: var(--container-width);

  &--lg {
    max-width: calc(var(--container-width) + 100px);
  }
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.disable-scroll {
  overflow: hidden;

  body {
    overflow: hidden;
  }
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background: #fff;

  &[type="number"] {
    -moz-appearance: textfield;
  }

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.text-center {
  text-align: center;
}

.section {
  counter-increment: section;
  overflow: hidden;
}

.section-counter {
  position: relative;
  display: flex;
  align-items: center;
  font-family: "Prosto One", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  color: #222;

  &::before {
    content: "(0" counter(section) ")";
    margin-right: 10px;
  }

  &::after {
    content: "";
    display: block;
    width: 27px;
    height: 12px;
    background: url("../img/counter-icon.svg") center no-repeat;
  }
}

.title {
  font-family: "Prosto One", sans-serif;
  font-weight: 400;
  font-size: 52px;
  line-height: 1.24;
  text-transform: uppercase;
  color: #08080d;
}

.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 8;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(#251e1e, 0.5);
  opacity: 0;
  transition: $menu-transition;

  &.is-active {
    opacity: 1;
  }
}

